const SpecialRoute: SpecialRouteType = {
  BACK: "#back",
  EXIT: "#exit",
};
const runBeforeTp: RunBeforeTpType = {};
function addRunBeforeTp(key: string, func: RunBeforeTpFuncType): boolean {
  if (runBeforeTp[key]) return false;
  runBeforeTp[key] = func;
  return true;
}
async function analyseSnowString(str: string, player: Player, list_key: string, index?: number) {
  if (/^#script /.test(str)) {
    let script_key = str.replace(/^#script /, "");
    // 
    if (script_key == undefined || script_key == "") {
      throw new Error(
        "(Anaylse String) No Script Specified. @" + player.name + "\nList Key: " + list_key + "\nSelect Index: " + index
      );
    }
    let exist = false;
    for (let key in strScripts) {
      if (key == script_key) {
        exist = true;
        break;
      }
    }
    if (exist == false)
      throw new Error(
        "(Anaylse String) Script Not Exist. @" + player.name + "\nList Key: " + list_key + "\nSelect Index: " + index
      );
    // 
    return strScripts[script_key](player, list_key);
  }
  return str;
}
/**
 * 
 * @param player 
 * @param list_key  jsonsnow
 * @returns 
 */
async function showSnowList(player: Player, list_key: string): Promise<boolean> {
  // data.snow
  if (data.snow == undefined) {
    throw new Error("Incorrect Data Format: [data.snow] Undefined.");
  }

  // 
  let exist = false;
  for (let key in data.snow) {
    if (key == list_key) {
      exist = true;
      break;
    }
  }
  if (exist == false) {
    throw new Error("Snow List [" + list_key + "] not Exist");
  }
  let timer = 0;
  while (true) {
    // 
    let actionFormData = new ActionFormData().title(data.snow[list_key].title);
    if (data.snow[list_key].body != "") {
      // Body 
      actionFormData = actionFormData.body(await analyseSnowString(data.snow[list_key].body, player, list_key));
    }
    if (data.snow[list_key].show_back) {
      // 
      actionFormData = actionFormData.button("");
    }

    // 
    let length = data.snow[list_key].list.length;
    if (length == 0) {
      actionFormData = actionFormData.button("()");
    } else {
      for (let i = 0; i < length; ++i) {
        actionFormData = actionFormData.button(
          await analyseSnowString(data.snow[list_key].list[i].name, player, list_key, i),
          (data.snow[list_key].list[i].icon ?? "") != ""
            ? "textures/blocks/" + data.snow[list_key].list[i].icon
            : undefined
        );
      }
    }

    // 
    let actionFormResponse = await actionFormData.show(player);

    //  Response
    if (actionFormResponse.canceled || actionFormResponse.selection == undefined) {
      return true;
    }

    if (data.snow[list_key].show_back && actionFormResponse.selection == 0) return false; // 
    if (length > 0) {
      let index = data.snow[list_key].show_back ? actionFormResponse.selection - 1 : actionFormResponse.selection;
      // 
      if (data.snow[list_key].list[index].tag != undefined && data.snow[list_key].list[index].tag.length > 0) {
        if (
          // tag undefined
          data.snow[list_key].list[index].tag.find((tag: string) => {
            return !player.hasTag(tag);
          }) != undefined
        ) {
          // tag
          let msfrNoTag = await new MessageFormData()
            .title("")
            .body("\n")
            .button1("")
            .button2("")
            .show(player);
          if (msfrNoTag.canceled || msfrNoTag.selection == 2) {
            return true; // 
          }
          break; // 
        }
      }
      switch (data.snow[list_key].list[index].type) {
        case "tp":
          {
            // 
            for (let key in runBeforeTp) {
              let result = await runBeforeTp[key](player, list_key);
              // tellMessage(moduleName, "cancel: " + result.cancel + " reason: " + (result.cancelReason ?? ""));
              if (result.cancel) {
                // let msfr = await new MessageFormData()
                //   .title("")
                //   .body("\n:" + (result.cancelReason ?? ""))
                //   .button1("")
                //   .button2("")
                //   .show(player);
                // if (msfr.selection == 1 || msfr.canceled) return false;
                // if (msfr.selection == 2) return true;
                return false;
              }
            }
            // 
            player.teleport(
              data.snow[list_key].list[index].location,
              world.getDimension(data.snow[list_key].list[index].dimension),
              0,
              0
            );
            // 
            if (data.snow[list_key].list[index].show) {
              tellMessage(
                senderName,
                "el" +
                  player.name +
                  " r al" +
                  (data.snow[list_key].list[index].show_name ?? data.snow[list_key].list[index].name)
              );
            }
            // 
            player.playSound("random.levelup", { volume: 100000 });
            // 
            player.runCommandAsync("title @s actionbar al");
            return true;
          }
          break;
        case "commands":
          {
            // 
            data.snow[list_key].list[index].commands.forEach((command: string) => {
              player.runCommandAsync(command);
            });
            // 
            if (data.snow[list_key].list[index].close) {
              return true;
            }
          }
          break;
        case "function":
          {
            // TODO
          }
          break;
        case "script":
          {
            let targetScriptKey = data.snow[list_key].list[index].script;
            if (targetScriptKey == undefined || targetScriptKey == "") {
              throw new Error(
                "No Script Specified. @" + player.name + "\nList Key: " + list_key + "\nSelect Index: " + index
              );
            }
            // 
            let exist = false;
            for (let key in snowScripts) {
              if (key == targetScriptKey) {
                exist = true;
                break;
              }
            }
            if (exist == false)
              throw new Error(
                "Script Not Exist. @" + player.name + "\nList Key: " + list_key + "\nSelect Index: " + index
              );
            // 
            let quit = true;
            try {
              quit = await snowScripts[targetScriptKey](player, list_key);
            } catch (e) {
              // 
              tellErrorMessage(moduleName, "Error in Script ol" + targetScriptKey);
              throw e;
            }
            // 
            if (quit) {
              return true;
            }
          }
          break;
        case "route":
          {
            let destination = data.snow[list_key].list[index].destination;
            //  Destination
            if (destination == "" || destination == undefined) {
              throw new Error(
                "(Route) No Destination Specified. @" +
                  player.name +
                  "\nList Key: " +
                  list_key +
                  "\nSelect Index: " +
                  index
              );
            }
            // 
            for (let key in SpecialRoute) {
              if (destination == SpecialRoute[key]) {
                switch (key) {
                  case "BACK":
                    return false;
                  case "EXIT":
                    return true;
                  default:
                    throw new Error("Unable to Deal with SpecialRoute." + key);
                }
              }
            }
            // 
            if ((await showSnowList(player, destination)) == true) return true;
          }
          break;
        default:
          throw new Error(
            "Snow Item Error! \nList Key: " +
              list_key +
              "\nSelect Index: " +
              index +
              "\nItem JSON: " +
              JSON.stringify(data.snow[list_key].list[index] ?? "ITEM_UNDEFINED")
          );
      }
    }
  }
  return true;
}
async function showSnowMenu(player: Player, entry_list_key?: string) {
  try {
    await showSnowList(player, entry_list_key ?? data.snow_main);
  } catch (e) {
    anaylseError(moduleName, e, "Error In [showSnowMenuNew]");
  }
}
function initCuberSnow() {
  // world.events.beforeChat.subscribe((e) => {
  //   if (e.message == "#") {
  //     e.cancel = true;
  //     showSnowMenuNew(e.sender);
  //   }
  // });
  world.events.beforeItemUse.subscribe(async (e) => {
    if (e.item.typeId != "minecraft:snowball" || e.source.typeId != "minecraft:player") return;
    if (!e.source.hasTag(data.settings.new_player.limit_tag)) {
      if (!data.settings.new_player.allow_snow) {
        let msfr = await new MessageFormData()
          .title("")
          .body("\n")
          .button1("")
          .button2("")
          .show(e.source as Player);
        return;
      }
      if (data.settings.new_player.guest_snow) {
        e.source.runCommandAsync("kill @e[type=snowball, c=1]");
        showSnowMenu(e.source as Player, "newPlayer_home");
        return;
      }
    }
    e.source.runCommandAsync("kill @e[type=snowball, c=1]");
    showSnowMenu(e.source as Player);
  });
  world.events.beforeChat.subscribe((e) => {
    if (e.message.startsWith("#") || e.message.startsWith("#checkin")) {
      let bCheckin = false;
      if (data.players[e.sender.name]) {
        if (data.players[e.sender.name].last_checkin == getDate()) {
          bCheckin = true;
        }
      }
      if (bCheckin) {
        // 
        tellMessage("bl", "e@" + e.sender.name + " r");
      } else {
        // 
        data.players[e.sender.name].last_checkin = getDate();
        data.players[e.sender.name].money += 10;
        data.players[e.sender.name].checkin_times += 1;
        saveData("@" + e.sender.name);
        tellMessage("al", "le" + e.sender.name + " rlar (+e10r)");
      }
    }
  });
  return { moduleName, moduleVersion };
}
export { initCuberSnow, showSnowMenu, addRunBeforeTp };
